

*, ::after, ::before {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

html {
    scroll-behavior: smooth;
}

body {
    padding: 0;
    margin: 0;

}

p {
    margin-bottom: 0;
}

img {
    border: none;
}

header, footer {
    margin: auto;
    max-width: 1920px;
}

main {
    margin: auto;
}

a {
    text-decoration: none;
    cursor: pointer;
}

button {
    border: none;
    cursor: pointer;
}

ul {
    list-style: none;
}



.box-3 {
    height: 200px;
    width: 100%;
}





// стили для контейнера
.container {
    height: 5000px;
    background: pink;
}

.sticky-element {
    position: sticky;
    top: -20px;
    border: 1px solid blue;
    width: 10%;
    /* Add any other styles for your sticky element */
  }